import { Component, OnInit, Injectable, Inject} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http'
import { iListings } from './Listings';
import { catchError } from '../../../node_modules/rxjs/operators';
import { pipe } from '../../../node_modules/rxjs';
@Injectable()
export class ListingsService {
  public thisurl: string = "";
  public alllistings: iListings[];

  ///http.get(originUrl + 'YOUR API URI HERE').subscribe(result => {...});
  //,@Inject('BUILD_URL') originUrl:string
  constructor(private http: HttpClient) {
    //    this.thisurl = originUrl;
    console.log('/Listings.json')
    console.log('inside Listings sevrvice  constructor')
  }
  private handleerror(error: HttpErrorResponse) {
    console.log(error);
    let message = 'Error status code ${error}';
    return Observable.throw(message);
  }
 getListings()  {
  console.log('inside listing service')
 return  this.http.get('/Listings.json')
      .pipe(
        catchError(this.handleerror)
   );


  }

 
}



