import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ListingsService } from '../LisitngService/listings.service';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse, HttpHeaders, HttpParams } from '@angular/common/http'
import { Component, OnInit, Injectable, Inject } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { iListings } from '../LisitngService/Listings';
import { catchError } from '../../../node_modules/rxjs/operators';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/do';
import { Conditional } from '../../../node_modules/@angular/compiler';
import { NgModuleCompileResult } from '../../../node_modules/@angular/compiler/src/ng_module_compiler';
import {  Pipe, PipeTransform } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TooltipModule } from 'ngx-bootstrap';
import { AlertModule } from 'ngx-bootstrap';
import { CarouselModule } from 'ngx-bootstrap/carousel';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
})
export class HomeComponent implements OnInit {
  isIn: boolean = false;
  isSeller: boolean = false;   // store state
  isRenter: boolean = false;
  isBuyer: boolean = false;
  public alllistings: iListings[];
  public listing: iListings;
  public Agentid: any;
  public showagent: boolean = true;
  errorMessage: string = "";
  searchText: string = "";
  btnIsSellerClick: boolean = false;
  btnIsBuyClick: Boolean = false
  constructor(private LS: ListingsService,private route: Router) { }
  ngOnInit() {
    this.LS.getListings()
      .subscribe(
      (data: iListings[] )=> {this.alllistings = data }

      , error => this.handleerror // error path

      ,
      () => console.log('completed homecomponent listing service call') //default path

    )

    // this.btnIsSellerClick = false;
  }
  private handleerror(error: HttpErrorResponse) {
    console.log(error);
    let message = 'Error status code ${error}';
    return Observable.throw(message);
  }
  toggleState() { // click handler
    // isIn: boolean = false;
    let isIn = false
    this.isIn = !this.isIn;

  }
  //  this.isIn = bool === false ? true : false;

  //contacts
  contacts() {
    //  this.isSeller = !this.isSeller;
    //this.route.navigate(['/seller']);
    //;  this.route.navigate(['/seller'])
    //   this.btnIsSellerClick = !this.btnIsSellerClick ;
    // this.route.navigate(['/seller'], { skipLocationChange: true });
    this.route.navigateByUrl('/contacts')


  }
  SellerInfo() {
    this.isSeller = !this.isSeller;
    //this.route.navigate(['/seller']);
    //;  this.route.navigate(['/seller'])
    //   this.btnIsSellerClick = !this.btnIsSellerClick ;
    // this.route.navigate(['/seller'], { skipLocationChange: true });
    this.route.navigateByUrl('/seller')


  }
  BuyerInfo() {
    this.isBuyer = !this.isBuyer;
    this.route.navigate(['/buyer'], { skipLocationChange: false });


  }
  RenterInfo() {
    this.isRenter = !this.isRenter;
    this.route.navigate(['/renter'], { skipLocationChange: false });
    //this.route.navigate(['/AgentDetails'], agent.AGENT_ID)
  }
  home() {
    this.route.navigate(['/home'], { skipLocationChange: false });

  }
}



