import { Component, OnInit, NgModule } from '@angular/core';
import { FormBuilder, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { Response, Headers, RequestOptions } from '@angular/http';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http'
import { BrowserModule } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { Observable } from 'rxjs/Observable';


@Component({
  selector: 'UploadComponent',
  templateUrl: './upload.component.html'
})
export class UploadComponent implements OnInit {
  submitted: boolean;
  public progress: number = 0;
  public message: string = '';
  public success: any;
  public data: any;
  public fileuploadedinfo: IFileUploadedInfo;
  public uploadProgress: number;
  public firstname: string;
  public clientId: number;
  private sub: any;
  constructor(private http: HttpClient, private rout: ActivatedRoute) { }

  ngOnInit() {
    //this.sub = this.rout.params.subscribe(params => {
    //  this.clientId = +params['id']; // (+) converts string 'id' to a number
    this.clientId = this.rout.snapshot.params['id'];
      // In a real app: dispatch action to load the details here.
    //});
  }
      
  //ngOnDestroy() {
  //  this.sub.unsubscribe();
  //}

  upload(files: any) {
    if (files.length === 0)
      return;

    const formData = new FormData();

    for (let file of files)
      formData.append(
        
        this.clientId.toString(), file.name, file

      );

    let headers = new Headers();
    headers.append('Accept', 'application/json');
    // DON'T SET THE Content-Type to multipart/form-data, 
    //You'll get the Missing content-type boundary error
    let options = new RequestOptions({ headers: headers });

    this.http.post<IFileUploadedInfo>('api/Upload', formData ,
        { reportProgress: true, observe: 'events' }
      )
      ///.map((res: any) => res.json())

      .subscribe(
        (resfileupload: any) => {
          //   this.success = res;
          // console.log(this.success.);
          if (resfileupload === HttpEventType.UploadProgress)
            console.log(Math.round(resfileupload.loaded / resfileupload.total * 100));

           // this.data =
           //   Math.round((resfileupload.loaded / resfileupload.total * 100));
              else if (resfileupload === HttpEventType.Response)
          console.log(resfileupload);
        
          this.fileuploadedinfo = resfileupload;
          this.submitted = true;
          console.log(this.fileuploadedinfo);

          this.success = true;
        },
        err => {
          console.log("Error occured");
          this.handleError(err);
        }
      );


  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        'Backend returned code ${error.status}, ' +
        'body was: ${error.error}');
    }
    // return an ErrorObservable with a user-facing error message
    return new ErrorObservable("Something Went Wrong");
  };
}

interface IFileUploadedInfo {
  clientID: number;
  count: number;

  filePath: string;
  size: number;
}



