import { Component, OnInit, NgModule } from '@angular/core';
import { FormBuilder, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import {  Response, Headers, RequestOptions } from '@angular/http';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { HttpClient, HttpRequest, HttpEventType, HttpResponse,HttpErrorResponse, HttpHeaders, HttpParams } from '@angular/common/http'
import { BrowserModule } from '@angular/platform-browser';
//import { NgxSpinnerService } from 'ngx-spinner';

import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { Observable } from 'rxjs/Observable';
import { observeOn } from 'rxjs/operators';
import { toString } from '@ng-bootstrap/ng-bootstrap/util/util';

@Component({
    selector: 'contacts',
    templateUrl: './contacts.component.html'
    ,
    styles: ['input.ng-invalid {border-left: 5px solid red} ']
})

 
export class contactsComponent implements OnInit {
    public contactsform: FormGroup;
    public submitted: boolean = false;
    public show: boolean = true;
    public buttonName: any= 'Hide';

    public success: any;
    public data: {};
    public contact: IContact;
    public contactId: string = '1';
    public fileuploadedinfo: IFileUploadedInfo;
    public uploadProgress: number;
    public progress: number;
    public message: string;
    public firstname: string;
  constructor(private _formBuilder: FormBuilder, private http: HttpClient, private rout: Router
    //, private spinner: NgxSpinnerService
  ) { }
  ngOnInit()
    {
        this.contactsform = this._formBuilder.group
            (
            {
                firstname: [null, [Validators.required, Validators.minLength(3)]],
                lastname: [null, [Validators.required]],
                phone: [null, [Validators.required]],

                email: [null, [Validators.required]],
                confirmemail: [null, [Validators.required]],
                //address: this._formBuilder.group(
                //    {
                street: [],
                city: [],
                state: [],
                zip: [null, [Validators.pattern('^[1-9][0-9]{4}$')]]
                //}          // )
            }
            )
  }
            toggle() {
              this.show = !this.show;
              //change the name of button
              if (this.show)
               this.buttonName = "Hide";
                  else
                this.buttonName = "Show";
                


                  }
              
    private handleerror(error: HttpErrorResponse) {
        console.log(error);
        let message = 'Error status code ${error}';
        return Observable.throw(message);
    }
  CallFileUploads(contact)

  {
    //JSON.parse(json)
   
    //['uri/cars'], {queryParams: {'page': 2}}
   // console.log(contactId);
    //  this.router.navigate(['/component-two', 456]);
  //}
    this.rout.navigate(['/Upload', contact.id]);
    
  }

  onSubmit({ value, valid }: { value: FormGroup, valid: boolean }) {
   

        this.submitted = true;
        console.log(value);

        let body = JSON.stringify(value);

        console.log(body);
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'my-auth-token'
            })

        };
          //setting body of the parameters to send via post 
   
    //this.spinner.show();
        this.http
            .post<IContact>('/api/contacts/', body, httpOptions)
                   ///.map((res: any) => res.json())
            .subscribe(
                res => {
                  //  console.log(res);
                    this.contact = res;
                  console.log(this.contact);
                  this.submitted = true;
                  this.buttonName = "Show";

                },
                err => {
                    console.log("Error occured");
                  this.handleerror(err);
                  this.submitted = false;
                  this.buttonName = "Hide";

            }
            ,
            () => {
                console.log("The POST Upload Files is now completed.");
            });
    //this.spinner.hide();
    this.show = false;
              }
    private handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                'Backend returned code ${error.status}, ' +
                'body was: ${error.error}');
        }
        // return an ErrorObservable with a user-facing error message
        return new ErrorObservable("Something Went Wrong");
    };


  upload(files: File[], con: IContact) {
   

        if (files.length === 0)
            return;

        const formData = new FormData();
   

    for (let file of files)
      formData.append(file.name, file);
       
        let myheaders = new Headers();
        myheaders.append('Accept', 'application/json');
        // DON'T SET THE Content-Type to multipart/form-data, 
        //You'll get the Missing content-type boundary error
    let myParams = new HttpParams()
      .append('ID', this.firstname );

    console.log(this.contact);

   // options.headers.append('ID', this.contact.LastName);
   // console.log(options.headers);
    myheaders.append('Accept', 'application/json');
    this.http.post<IFileUploadedInfo>('api/Upload', formData, { reportProgress:true,params:myParams})
             ///.map((res: any) => res.json())
            .subscribe(
            (resfileupload: any) => {
              if (resfileupload.type = HttpEventType.UploadProgress)
                this.progress = Math.round(100 * resfileupload.loaded / resfileupload.total);
              else if (resfileupload.type === HttpEventType.Response)
                this.message = resfileupload.body.toString();
              console.log(this.success);
                this.fileuploadedinfo = resfileupload;
                this.submitted = true;
                console.log(this.fileuploadedinfo);
                this.success = true;
            },
            err => {
                console.log("Error occured");
                this.handleerror(err);
            }
            );
       
       
    }

     
    }

 interface IContact {
     Id: number;
     FirstName: string;
     LastName: string;
     Phone: string;
     Email: string;
     ConfirmEmail: string;
     street: string;
     City: string;
     ZIP: string;
     IsComplete: boolean;
}
interface IFileUploadedInfo {
  count: number;
 
  filePath: string;

  size: number;

}



